import React from "react";
import news from "../../assets/img/1592292364689.jpg";
const News = () => {
  return (
    <section className="news">
      <div className="container ">
        <h1>News</h1>
        <div>
          <img src={news} />
        </div>
      </div>
    </section>
  );
};

export default News;
