import React from "react";
const Github = () => {
  return (
    <section className="openSource">
      <div className="container ">
        <h1>Open Source Contribution</h1>

        <div>
          <div className="project thismypc">
            <a href="https://github.com/supunlakmal/thismypc" target="_bank">
              <span className="title">This My PC</span>
              <div className="icon"></div>
            </a>
          </div>

          <div className="project electron">
            <a href="https://github.com/electron/electron" target="_bank">
              <span className="title">Electron</span>
              <div className="icon"></div>
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Github;
